  display: inline-block;
  text-decoration: inherit;
  width: 1em;
  margin-right: 0.2em;
  text-align: center;
  font-variant: normal;
  text-transform: none;
  line-height: 1em;
  margin-left: 0.2em;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-down:before {
  content: "\e800";
}

.icon-left:before {
  content: "\e801";
}

.icon-right:before {
  content: "\e802";
}

.icon-up:before {
  content: "\e803";
}

.icon-zoom-in:before {
  content: "\e804";
}

.icon-mail:before {
  content: "\e805";
}

.icon-down-open:before {
  content: "\f004";
}

.icon-up-open:before {
  content: "\f005";
}

.icon-right-open:before {
  content: "\f006";
}

.icon-left-open:before {
  content: "\f007";
}

.icon-twitter:before {
  content: "\f099";
}

.icon-mail-alt:before {
  content: "\f0e0";
}

.icon-linkedin:before {
  content: "\f0e1";
}

.icon-youtube:before {
  content: "\f16a";
}

.icon-facebook:before {
  content: "\f09a";
} /* '' */
.icon-instagram:before {
  content: "\f16d";
} /* '' */
