/***************************************************
    APP BASE STYLES
 ***************************************************/
html {
  font-size: 62.5%;
  scroll-behavior: unset;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: var(--body-font-size);
  color: #262626;
  background: #fff;
}

#main-wrapper {
  padding-top: var(--header-height);
  flex-grow: 1;
}

::selection {
  color: #fff;
  background: #0455c0;
}

figure {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
}

video {
  display: block;
}

.body-loading {
  overflow: hidden;
}

.body-loader {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #fff;
  z-index: 999999999;
}
.body-loader.fadeout {
  animation: fadeLoaderOut 0.2s 0.2s linear forwards;
}

@keyframes fadeLoaderOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
