/******************************************************
    ARCHIVE SERVICES
 ******************************************************/
.services-archive {
  --space-top: 8rem;
}
.services-archive #top-archive {
  padding-top: var(--space-top);
  scroll-margin-top: var(--header-scrolled-height);
}
.services-archive .col-side {
  padding-top: var(--space-top);
}
.services-archive .taxonomy-list {
  padding-bottom: 2rem;
}
.services-archive .taxonomy-list .title {
  margin-bottom: 2rem;
}
.services-archive .taxonomy-list .title button {
  all: unset;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
  width: 100%;
  padding: 1rem 1.5rem;
  font-size: 2rem;
  color: #fff;
  background: #0455c0;
}
.services-archive .taxonomy-list .title button::after {
  content: "-";
}
.services-archive .taxonomy-list .title button.collapsed::after {
  content: "+";
}
.services-archive .taxonomy-list .list {
  margin-bottom: 0;
  padding-bottom: 1rem;
}
.services-archive .taxonomy-list .list li {
  margin-bottom: 2rem;
}
.services-archive .taxonomy-list label {
  position: relative;
  text-transform: none;
  cursor: pointer;
  font-weight: 700;
}
.services-archive .taxonomy-list label input {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  z-index: -1;
}
.services-archive .taxonomy-list label input + span {
  position: relative;
  padding-left: 3rem;
  display: block;
}
.services-archive .taxonomy-list label input + span::before {
  content: "";
  position: absolute;
  top: 0.4rem;
  left: 0;
  width: 1.6rem;
  height: 1.6rem;
  border: 1px solid currentColor;
}
.services-archive .taxonomy-list label input + span::after {
  content: "";
  position: absolute;
  top: 0.8rem;
  left: 0.4rem;
  width: 0.8rem;
  height: 0.8rem;
  background: #0455c0;
  opacity: 0;
  transition: opacity 0.2s;
}
.services-archive .taxonomy-list label input:checked + span::after {
  opacity: 1;
}
.services-archive .taxonomy-list label input:focus + span::before {
  outline: 2px dotted currentColor;
  outline-offset: 3px;
}
.services-archive .taxonomy-list label a {
  color: inherit;
}
.services-archive .clear-filters {
  all: unset;
  font-size: 1.4rem;
  font-weight: 700;
  text-decoration: underline;
  cursor: pointer;
}
.services-archive .clear-filters.hidden {
  display: none;
}
.services-archive .loader {
  position: absolute;
  z-index: -1;
  inset: 1rem;
  top: var(--space-top);
  display: grid;
  place-items: center;
  background: rgba(4, 85, 192, 0.5);
  opacity: 0;
  transition: opacity 0.3s;
}
.services-archive .loader::before {
  content: "";
  display: block;
  width: 8rem;
  height: 8rem;
  border: 3px solid transparent;
  border-top-color: #0455c0;
  border-bottom-color: #0455c0;
  border-radius: 50%;
  animation: rotation 0.8s ease infinite;
}
.services-archive .loader.active {
  z-index: 2;
  opacity: 1;
}
.services-archive .services-pagination {
  padding: 6rem 0 8rem;
}
.services-archive .services-pagination .cta {
  gap: 1rem;
}
.services-archive .services-pagination .prev.hidden,
.services-archive .services-pagination .next.hidden {
  display: none;
}
.services-archive .services-pagination .prev::before {
  transform: rotate(180deg);
}
.services-archive .services-pagination .next {
  flex-direction: row-reverse;
}

@keyframes rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.box-services {
  display: block;
  position: relative;
  color: #fff;
  overflow: hidden;
}
.box-services::before {
  content: "";
  position: absolute;
  z-index: 1;
  inset: 0;
  background: linear-gradient(to top, #0455c0, transparent);
}
.box-services .image {
  width: 100%;
  transition: scale 0.3s;
}
.box-services .title {
  position: absolute;
  z-index: 2;
  bottom: 1rem;
  left: 1rem;
  right: 1rem;
  font-size: 2rem;
  font-weight: 700;
}
.box-services:hover, .box-services:focus-visible {
  color: #fff;
}
.box-services:hover .image, .box-services:focus-visible .image {
  scale: 1.2;
}

