/******************************************************
    SITE HEADER
******************************************************/
.site-header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 20;
  background: #fff;
  filter: drop-shadow(0 1rem 2rem rgba(0, 0, 0, 0.08));
